import React, { useState } from "react";
import Select from "react-select";
import { Link } from "react-router-dom";

// Types
import { TemporalData } from "./types";
// Data
import TemporalDataJson from "./data/temporaldata.json";
// Components
import LineChart from "./LineChart";
// Images
import ChevronRight from "../icons/chevron-right.svg";
// CSS
import "./TemporalTrendView.css";

const data: TemporalData[] = TemporalDataJson.visualization_data;

const typeSet = new Set(data.map((x) => x.violence_type));
const typeOptions = Array.from(typeSet).map((x) => ({ value: x, label: x }));
const ageGroupOptions = TemporalDataJson.labels.map((x, i) => ({
  value: i,
  label: x,
}));

function TemporalTrendView() {
  const [filterType, setFilterType] = useState<string>(typeOptions[0].value);
  const [filterAgeGroup, setFilterAgeGroup] = useState<number>(
    ageGroupOptions[0].value
  );
  const [currentState, setCurrentState] = useState(0);

  const entry = data.find((x) => x.violence_type === filterType)?.age_groups?.[
    filterAgeGroup
  ];
  const daysBefore = -92;

  // Define annotations
  const annotations = [];
  if (currentState >= 1) {
    annotations.push({ text: "Dia da identificao de violncia", xPos: 0 });
  }
  if (currentState >= 2) {
    annotations.push({ text: `${-daysBefore} dias antes`, xPos: daysBefore });
  }

  const texts = [
    [
      <>
        O grfico ao lado mostra como a chance de uma mulher ou menina procurar
        atendimento na sade aumenta nos dias prximos a uma situao de
        violncia.
      </>,
      <>
        <strong>
          Este grfico representa um total de {entry?.n} mulheres/meninas
        </strong>{" "}
        com os filtros atuais aplicados.
      </>,
      <>
        O eixo de baixo (<strong>horizontal</strong>) representa o tempo: os
        dias antes e depois do momento em que a violncia foi reconhecida por
        algum servio de sade. Isso pode acontecer, por exemplo, com uma
        notificao no Sinan, uma internao por causa externa ou quando a
        paciente menciona a violncia no pronturio do e-SUS APS.
      </>,
      <>
        O eixo da lateral (<strong>vertical</strong>) mostra a probabilidade de
        que o nmero de atendimentos aumente naquele dia, comparado com outros
        perodos.
      </>,
      <>
        <strong>Essa probabilidade j comea alta</strong>: mulheres e meninas
        em situao de violncia tendem a procurar mais os servios de sade,
        mesmo antes de falar sobre a violncia explicitamente.
      </>,
    ],
    [
      <>
        O <strong>dia 0</strong>, destacado com uma linha, representa o momento
        em que a violncia  registrada de alguma forma no sistema de sade.
      </>,
      <>
        Observe que, nos dias anteriores ao Dia 0, h uma tendncia de aumento
        nos atendimentos. Isso significa que, em muitos casos, a vtima j
        estava buscando ajuda, mesmo que ainda no tenha dito ou no tenha sido
        identificada como algum em situao de violncia.
      </>,
    ],
    [
      <>
        Quando toda a populao feminina  analisada em conjunto,
        considerando-se todos os tipos de violncia, o padro geral que surge 
        o de que os atendimentos das vtimas tendem a aumentar cerca de{" "}
        <strong>{daysBefore} dias</strong> antes da data em que a violncia 
        identificada.
      </>,
      <>
        Esse padro apresenta uma possibilidade de se identificar casos antes de
        um agravamento. Esse  um padro geral e, portanto, deve ser
        relativizado para cada faixa etria para um direcionamento mais preciso.
      </>,
      <>
        As faixas etrias nessa visualizao so mais abrangentes porque 
        necessrio um nmero maior de pontos para observar padres.
      </>,
    ],
  ];

  return (
    <div className="temporal-trend-content content">
      <div className="filter-container">
        <div className="filter-field-container">
          <span>Natureza da violncia</span>
          <Select
            options={typeOptions}
            value={typeOptions.find((x) => x.value === filterType)}
            onChange={(option: any) => setFilterType(option.value)}
            placeholder="Selecione..."
          />
        </div>
        <div className="filter-field-container">
          <span>Faixa etria</span>
          <Select
            options={ageGroupOptions}
            value={ageGroupOptions.find((x) => x.value === filterAgeGroup)}
            onChange={(option: any) => setFilterAgeGroup(option.value)}
            placeholder="Selecione..."
          />
        </div>
      </div>
      <div className="temporal-trend-main-container">
        <div className="temporal-trend-left-container">
          {texts[currentState].map((paragraph, idx) => (
            <p key={idx}>{paragraph}</p>
          ))}
          <div className="temporal-trend-button-wrapper">
            {currentState === [0, 1, 2].length - 1 ? (
              <Link to="/nota-tecnica#padroes-no-tempo" className="no-underline">
                <button className="button">
                  <span>Entenda como esses dados foram analisados</span>
                </button>
              </Link>
            ) : (
              <button
                onClick={() => setCurrentState(currentState + 1)}
                className="button"
              >
                <span>Avanar</span>
                <img alt="" src={ChevronRight} />
              </button>
            )}
          </div>
        </div>
        <div>
          <LineChart data={entry?.data || []} annotations={annotations} />
          <div className="carousel-controls">
            {[0, 1, 2].map((index) => (
              <button
                key={index}
                className={`carousel-button ${
                  currentState === index ? "active" : ""
                }`}
                onClick={() => setCurrentState(index)}
              >
                
              </button>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default TemporalTrendView;
