import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";

import HeroBackground from "../img/hero_background_v2.png";
import "./Homepage.css";

function Homepage() {
  const location = useLocation();
  useEffect(() => {
    if (location.hash) {
      setTimeout(() => {
        const id = location.hash.replace("#", "");
        const el = document.getElementById(id);
        if (el) {
          el.scrollIntoView({ behavior: "smooth", block: "start" });
        }
      }, 0);
    }
  }, [location]);

  return (
    <div className="homepage">
      <section className="hero">
        <div className="hero-text">
          <h1>Painel da violncia de gnero e sade no Recife</h1>
          <p>
             possvel identificar sinais de violncia em atendimentos de rotina
            da ateno primria em sade? Ns acreditamos que sim. Com o uso do
            pareamento de bancos de dados da sade e tecnologia lingustica, a
            Vital Strategies e FrameNet Brasil utilizaram inteligncia
            artificial para analisar os textos de pronturios eletrnicos de
            atendimentos da ateno primria de mulheres de Recife.
          </p>
          <p>
            Os casos com notificao de violncias no SINAN e pronturios foram
            utilizados para treinar o modelo de inteligncia artificial para a
            compreenso dos padres de sade de mulheres vivenciando a
            violncia. A partir desse aprendizado, o modelo foi aplicado aos
            demais atendimentos para identificar possveis ocorrncias no
            notificadas. A IA permite estimar o cenrio de casos provveis
            no-notificados por unidade de sade.
          </p>
        </div>
        <img src={HeroBackground} alt="" />
      </section>

      <section className="explore">
        <h2>Explore nossos dados</h2>
        <p>
          Utilize nossa ferramenta de visualizao interativa para explorar uma
          ampla gama de indicadores relacionados  violncia contra a mulher.
        </p>
      </section>
    </div>
  );
}

export default Homepage;
