import React, { useState } from "react";

// Types
import { TerritoryDetailProps } from "./types";
// Utils
import { formatPercentage, formatRatePer10k } from "./utils";
// Image
import Close from "../icons/close.svg";
import Help from "../icons/help.svg";
// CSS
import "./TerritoryDetail.css";

function TerritoryDetail({ data }: TerritoryDetailProps) {
  const [isHelpOpen, setIsHelpOpen] = useState<boolean>(false);
  const hasUnitData = data.units.filter((x) => x.esus_users > 0).length > 0

  return (
    <div className="territory-detail-container">
      <div className="territory-detail-info-box">
        <div className="territory-detail-info-box-header">
          <h3>{`Bairro ${data.name}`}</h3>
          <img
            src={Help}
            alt="Help"
            onClick={() => setIsHelpOpen(!isHelpOpen)}
          />
        </div>
        <div className="territory-detail-info-box-body">
          <div className="territory-detail-info-box-row">
            <span>Taxa de subnotificao por 10 mil</span>
            <span>{formatRatePer10k(data.subnotification_rate)}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Categoria</span>
            <span>{data.category}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Populao feminina</span>
            <span>{data.population}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Usurias da ateno bsica</span>
            <span>{data.resident_esus_users}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Casos subnotificados</span>
            <span>{data.resident_underreported_cases}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Casos provveis</span>
            <span>{data.resident_suspected_cases}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Notificaes no SINAN</span>
            <span>{data.resident_sinan_notifications}</span>
          </div>
        </div>
      </div>

      {hasUnitData ? (
        <div className="territory-detail-units-table">
          <div className="territory-detail-units-table-header">
            <div>Unidades de Sade</div>
            <div>Casos Provavis</div>
            <div>Usurias</div>
            <div>% Casos / Usurias</div>
          </div>
          {data.units
            .filter((x) => x.esus_users > 0)
            .map((healthUnitData) => (
              <div
                className="territory-detail-units-table-row"
                key={healthUnitData.unit_name}
              >
                <div>{healthUnitData.unit_name}</div>
                <div>{healthUnitData.suspected_cases || 0}</div>
                <div>{healthUnitData.esus_users || 0}</div>
                <div>
                  {formatPercentage(
                    (healthUnitData.suspected_cases || 0) /
                    (healthUnitData.esus_users || 1),
                    1
                  )}
                </div>
              </div>
            ))}
        </div>
      ) : (
        <p className="territory-detail-message-no-unit">
          No h registro de unidade de sade localizada neste bairro.
        </p>
      )}

      {isHelpOpen && (
        <>
          <div className="territory-detail-backdrop" onClick={() => setIsHelpOpen(false)} />

          <div className="territory-detail-help-box">
            <div className="territory-detail-help-box-content">
              <h3 className="help-box-title">Entenda os Indicadores:</h3>

              <dl className="help-definition-list">
                <dt><strong>Taxa de Subnotificao</strong></dt>
                <dd>
                  Calculada a partir dos resultados do modelo de inteligncia artificial, esta taxa  o n de casos (provveis) de violncia sinalizados pelo modelo de IA que no foram notificados dividido pelo total de mulheres atendidas na ateno primria e multiplicados por 10 mil.
                </dd>

                <dt><strong>Categoria</strong></dt>
                <dd>
                  Classificao da taxa de subnotificao do bairro em relao  mdia de casos provveis (identificados pelo modelo de IA) do municpio.
                </dd>

                <dt><strong>Populao feminina</strong></dt>
                <dd>
                  Populao estimada com base nos dados do Censo 2010.
                </dd>

                <dt><strong>Usurias da ateno bsica</strong></dt>
                <dd>
                  Nmero de mulheres que residem no bairro e possuem pelo menos um registro de atendimento em servio da ateno bsica no perodo selecionado.
                </dd>

                <dt><strong>Casos subnotificados</strong></dt>
                <dd>
                  Nmero de casos em que h certeza de violncia, mas no h notificao. Identificados pelo cdigo CID de atendimentos, hospitalizao ou bito ou por identificao de relato direto de violncia em pronturio com auxlio de IA de anlise semntica.
                </dd>

                <dt><strong>Casos provveis</strong></dt>
                <dd>
                  Numro de casos em que a IA identificou padres textuais no pronturio eletrnico similares ao de casos notificados de violncia.
                </dd>

                <dt><strong>Notificaes no SINAN</strong></dt>
                <dd>
                  Numro de notificaes registradas no SINAN de mulheres que residem no bairro durante perodo selecionado.
                </dd>
              </dl>
            </div>

            <div className="territory-detail-help-box-close-wrapper">
              <img src={Close} alt="Fechar ajuda" title="Fechar" onClick={() => setIsHelpOpen(false)} />
            </div>
          </div>
        </>
      )}


    </div>
  );
}

export default TerritoryDetail;